import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

function getPermissions() {
  if (localStorage.getItem('token')) {
    try {
      return JSON.parse(atob(localStorage.getItem('token').split('.')[1])).permissions
    }
    catch {
      return {}
    }
  } else if (sessionStorage.getItem('token')) {
    try {
      return JSON.parse(atob(sessionStorage.getItem('token').split('.')[1])).permissions
    }
    catch {
      return {}
    }
  } else {
    return {}
  }
}

export default new Vuex.Store({
  state: {
    status: '',
    token: localStorage.getItem('token') || sessionStorage.getItem('token') ||'',
    username: localStorage.getItem('username') || sessionStorage.getItem('username') || '',
    permissions: getPermissions()
  },
  mutations: {
    auth_request(state){
      state.status = 'loading'
    },
    auth_success(state, payload){
      state.status = 'success'
      state.token = payload.token
      state.username = payload.username
      state.permissions = payload.permissions
    },
    auth_error(state){
      state.status = 'Server error'
    },
    auth_wrong(state){
      state.status = 'Wrong login or password'
    },
    auth_logout(state){
      state.status = ''
      state.token = ''
      state.permissions = {}
    },
  },
  actions: {
    login({commit}, data){
      return new Promise((resolve, reject) => {
        localStorage.setItem('login', data.login)
        commit('auth_request')
        axios({url: 'backend/auth', data: data.user_data, method: 'POST' })
        .then(resp => {
          if (resp.data.result != 'success') {
            commit('auth_wrong')
            localStorage.removeItem('token')
            localStorage.removeItem('username')
            sessionStorage.removeItem('token')
            sessionStorage.removeItem('username')
            reject('Wrong login or password')
          } else {
            const token = resp.data.token
            const username = resp.data.username
            let payload = JSON.parse(atob(token.split('.')[1]));
            if (data.remember) {
              localStorage.setItem('token', token)
              localStorage.setItem('username', username)
            } else {
              sessionStorage.setItem('token', token)
              sessionStorage.setItem('username', username)
            }
            axios.defaults.headers.common['Authorization'] = token
            commit('auth_success', {
              'token': token, 
              'username': username, 
              'permissions': payload['permissions']
            })
            resolve(resp)
          }
        })
        .catch(err => {
          commit('auth_error')
          localStorage.removeItem('token')
          localStorage.removeItem('username')
          sessionStorage.removeItem('token')
          sessionStorage.removeItem('username')
        reject(err)
        })
      })
    },
    logout({commit}){
      return new Promise((resolve, reject) => {
        commit('auth_logout')
        localStorage.removeItem('token')
        localStorage.removeItem('username')
        axios({url: 'backend/auth', method: 'DELETE' })
        .then(resp => {
          delete axios.defaults.headers.common['Authorization']
          resolve(resp)
        })
        .catch(err => {
          reject(err)
        })
      })
    }  
  },
  modules: {
  },
  getters : {
    isLoggedIn: state => !!state.token,
    username: state => state.username,
    permissions: state => state.permissions,
  }
})
