<template>
  <v-app id="app">
    <v-navigation-drawer
      v-model="drawer"
      :clipped="$vuetify.breakpoint.lgAndUp"
      v-if="$route.name != 'login'"
      app
    >
      <v-list dense>
        <template v-for="item in left_menu_items">
          <v-list-group
            v-if="item.children"
            :key="item.text"
            v-model="item.model"
            :prepend-icon="item.model ? item.icon : item['icon-alt']"
            append-icon=""
          >
            <template v-slot:activator>
              <v-list-item-content>
                <v-list-item-title>
                  {{ item.text }}
                </v-list-item-title>
              </v-list-item-content>
            </template>
            <v-list-item
              v-for="(child, i) in item.children"
              :key="i"
              link
              @click='itemClick(child)'
            >
              <v-list-item-action v-if="child.icon">
                <v-icon>{{ child.icon }}</v-icon>
              </v-list-item-action>
              <v-list-item-content>
                <v-list-item-title>
                  {{ child.text }}
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list-group>
          <v-list-item
            v-else
            :key="item.text"
            link
            @click='itemClick(item)'
          >
            <v-list-item-action>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>
                {{ item.text }}
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </template>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar
      :clipped-left="$vuetify.breakpoint.lgAndUp"
      app
      color="blue darken-3"
      dark
      v-if="$route.name != 'login'"
    >
      <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title
        style="width: 300px"
        class="ml-0 pl-4"
      >
        <span class="hidden-sm-and-down">Equipment</span>
      </v-toolbar-title>
      <v-text-field
        flat
        solo-inverted
        hide-details
        prepend-inner-icon="mdi-magnify"
        label="Search (not working yet)"
        class="hidden-sm-and-down"
      ></v-text-field>
      <v-spacer></v-spacer>
      <v-btn icon>
        <v-icon>mdi-bell</v-icon>
      </v-btn>
      <span>
        {{ $store.getters.username }}
      </span>
    </v-app-bar>
    <v-content>
      <router-view />
    </v-content>
    <template>
      <dialog-confirm 
        v-model="confirm_dialog"
        title="Are you sure?"
        :message="confirm_message"
        :confirm="confirmLogout"
      >
      </dialog-confirm>
    </template>
  </v-app>
</template>

<script>
  import left_menu from './router/left_menu'
  import DialogConfirm from './components/Dialog.confirm.vue'

  export default {
    data: () => ({
      confirm_dialog: false,
      confirm_message: '',
      drawer: null,
    }),
    methods: {
      itemClick: function(item) {
        if (item.text == 'Logout') {
          this.logout('Exit from application?')
          return
        }
        if (typeof item.route !== 'undefined' && this.$route.name != item.route) {
          this.$router.push({name: item.route})
        }
      },
      confirmLogout() {
        this.$store.dispatch('logout')
        .then(() => {
          this.$router.push('/login')
        })
        this.confirm_dialog = false
      },
      logout(confirm_message) {
        this.confirm_dialog = true
        this.confirm_message = confirm_message
      }
    },
    computed: {
      left_menu_items(){
        // check conditions to show menu item
        return left_menu.filter(item => {
          if (item['requires']['isLoggedIn'] == false) {
            return !this.$store.getters.isLoggedIn
          } else {
            if (this.$store.getters.permissions.superuser) {
              return true
            }
            else if (item['requires']['permission']) {
              return item['requires']['permission'].includes(this.$store.getters.permissions[item.route])
            } else {
              return true
            }
          }
        })
      },
      isLoggedIn(){
        return this.$store.getters.isLoggedIn
      }
    },
    created: function () {
      this.$http.interceptors.response.use(undefined, (err) => {
        return new Promise( () => {
          if (err.response.status === 401) {
            this.$store.dispatch("logout")
            .then(() => {
              this.$router.push('/login')
            })
          }
          throw err;
        });
      });
    },
    components: {
      DialogConfirm,
    }
  }
</script>